@import "https://fonts.googleapis.com/css2?family=BenchNine&family=Courier+Prime:wght@400;700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.circles {
  background-color: #071952;
  justify-content: center;
  gap: 20px;
  padding: 50px;
  display: flex;
}

.circle {
  width: 50px;
  height: 50px;
  background-color: var(--light-teal);
  border: 5px solid var(--teal);
  border-top: 5px solid var(--light-teal);
  border-radius: 50%;
  transition: all .25s ease-in-out;
  animation-name: spin;
  animation-duration: 3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  position: relative;
  transform: rotate(180deg);
}

@keyframes spin {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

.small-circle {
  width: 30px;
  height: 30px;
  background-color: var(--teal);
  border-radius: 50%;
  transition: all .5s ease-in-out;
  transform: translateX(0%)translateY(-10%);
}

.smallest-circle {
  width: 10px;
  height: 10px;
  background-color: var(--teal);
  border-radius: 50%;
  transition: all .5s ease-in-out;
  transform: translateX(300%)translateY(-280%);
}

.circle:hover {
  cursor: pointer;
  animation-play-state: paused;
}

.circle:hover.circle {
  border-top: 5px solid var(--dark-color);
  border-color: var(--dark-color);
  background-color: #fff;
}

.circle:hover .small-circle {
  background-color: var(--dark-color);
  transform: translateX(32%)translateY(32%);
}

.circle:hover .smallest-circle {
  background-color: var(--dark-color);
  transform: translateX(200%)translateY(-100%);
}

.circle2 {
  width: 53px;
  height: 53px;
  background-color: var(--light-teal);
  border-radius: 50%;
  animation-name: spin2;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.small-circle2 {
  width: 50px;
  height: 50px;
  background-color: var(--dark-teal);
  border-radius: 50%;
  transition: all .5s ease-in-out;
}

@keyframes spin2 {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

.circle2:hover {
  cursor: pointer;
  background-color: #fff;
}

.circle2:hover .small-circle2 {
  cursor: pointer;
  background-color: var(--dark-color);
  transform: scale(.5);
}

